import flowers from "../flowers";

const Home = () => {
    return(
        <section id="content">
            <div class="container">
                <div class="title text-center">
                    <h1 class="display-1">Flowers</h1>
                </div>
                {flowers.map((item) => {
                    return (
                        <div class="row mt-2">
                            <div class="col-md-4"></div>
                            <div class="col-md-4">
                                <div class="card text-center mt-2">
                                    <img
                                        src={item.picture}
                                        class="card-img-top"
                                    />
                                    <div class="card-body">
                                        <h5 class="card-title">{item.name}</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    );
                })}
            </div>
        </section>
         
    );
};

export default Home;

